import { Categories, IToDo, SAVETODOS_KEY, toDoState } from "../atoms";
import { useSetRecoilState } from "recoil";
import React from "react";
import styled from "styled-components";

const List = styled.li`      
  margin: 10px auto;
  width: 100%;
  padding: 10px;
  background-color: #f6e58d;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
  border-radius: 5px;   
  display: flex;
  //justify-content: space-between;   
`;
const Text = styled.span`
  font-size: 15px;
  color: rgba(0,0,0);
  line-height: 1.5;
  flex: 0 1 92%; 
`;
const Input = styled.input`
  width: 15px;
  height: 15px;
  margin-right: 5px;
`;
const ButtonGroup = styled.div`  
  flex: 1 1 25%;
`;
const Button = styled.button`  
  border: 0;
  border-radius: 3px;  
  background-color: transparent;
  font-size: 15px;
  cursor: pointer;
  &:hover {
    text-shadow: 1px 1px 2px gray;
  }  
`;

function ToDo({ text, id, category}:IToDo) {
  const setToDos = useSetRecoilState(toDoState);    
  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    const { currentTarget: { name } } = event;
    setToDos((oldToDos) => {
      const targetIndex = oldToDos.findIndex(toDo => toDo.id === id); // ÌÅ¥Î¶≠ÌñàÏùÑÎïå Ìï¥Îãπ Î≤àÌò∏ Ï∂îÏ∂ú
      const newToDo = {text, id, category: name as Categories};
      const newToDos = [...oldToDos.slice(0, targetIndex),
        newToDo, ...oldToDos.slice(targetIndex + 1),
      ];
      const stringfyTodos = JSON.stringify(newToDos);
      localStorage.setItem(SAVETODOS_KEY,stringfyTodos);
      return newToDos; // toDo Ï∂îÍ∞ÄÌïòÎäî Í≥ºÏ†ï
    })
  };
  const DelToDos = (event: React.FormEvent<HTMLButtonElement>) => {
    const {
      currentTarget: { parentElement },
    } = event;
    setToDos((oldToDos) => {
      const newToDos = oldToDos.filter(toDo => toDo.id !== Number(parentElement?.id));
      //Ìï¥ÎãπÌïòÎäî idÎßå ÏïÑÎãå Í≤ÉÎßå ÎÇ®Í≤®Îëò Í≤É.
      const stringfyTodos = JSON.stringify(newToDos);
      localStorage.setItem(SAVETODOS_KEY,stringfyTodos);
      return newToDos; //ÏÇ≠Ï†úÌï† Î∂ÄÎ∂Ñ filterÎ°ú Í±∞Î•∏ ÌõÑ Î∞∞Ïó¥ Ïû¨ÏÉùÏÑ± Î∞è Ï†ÄÏû•
  });
};
  
  return (
    <List id = {id as any}>            
      <Text>
        <Input type = "checkbox" />
        {text}
      </Text>
      <ButtonGroup>
        {category !== Categories.DOING && (
        <Button name = {Categories.DOING} onClick = {onClick}>üü¢</Button>
        )}
        {category !== Categories.TO_DO && (
        <Button name = {Categories.TO_DO} onClick = {onClick}>üü°</Button>
        )}
        {category !== Categories.DONE && (
        <Button name = {Categories.DONE} onClick = {onClick}>üîµ</Button>
        )} 
        {/* // Ìï¥ÎãπÎêòÎäî Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÌÅ¥Î¶≠ÌïòÏßÄ ÏïäÏùÑ Îïå Î≤ÑÌäºÏùÑ Î≥¥Ïó¨Îùº */}                
      </ButtonGroup>   
      <Button onClick = {DelToDos}>X</Button>
    </List>
  );
}

export default ToDo;